<template>

        <MyCarousel />
        <MySelector />
        <MyPackage @upgrade="openRegistration"/>
      
        <LaunchButton v-if="showLaunchButton" />
        <tmReg v-if="showRegistration" :project="selectedPackage" @close="showRegistration = false" />
</template>

<script>
import { ref } from 'vue';
import LaunchButton from './LaunchButton.vue';
import MyCarousel from './MyCarousel.vue'; 
import MyPackage from './MyPackage.vue'; 

import MySelector from './MySelector.vue'; 
import tmReg from './tmReg.vue'; 


export default {
  setup() {
    const count = ref(0);
    const selectedPackage = ref('');
    const showRegistration = ref(false);

    const increaseCount = (i) => {
      count.value += i;
    };

  
    const openRegistration = (packageName) => {
      showRegistration.value = true;
      console.log('Received upgrade event with package:', packageName);
      selectedPackage.value = packageName;
    
    };

    return {
 
      count,
      selectedPackage,
      showRegistration,
      openRegistration,
      increaseCount,
    };
  },
  components: {
    tmReg,
    MyCarousel,
    MyPackage, 
    MySelector,

    LaunchButton,
    
  },data() {
    return {
      showLaunchButton: false, // Boolean to control visibility
    };
  },
};
</script>


